.header {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__link {
    color: #5A5B6A;
  }
  &__reg {
    color: #5A5B6A;
  }
  &__form {
    position: relative;
  }
  &__input {
    padding: 8px 44px;
    border-radius: 99px;
    background-color: #EDEDF0;
    outline: none;
    width: 600px;
    font-weight: 400;
    font-size: 20px;
    line-height: 158%;
    letter-spacing: 0.0075em;
    color: #19191D;
    @include small-tablet {
      width: auto;
    }
    @include mobile {
      width: 300px;
    }
  }
  &__search {
    background-image: url('../img/search.svg');
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translateY(-50%);
    width: 32px;
    height: 32px;
  }
  &__category {
    color: var(--blue-color);
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__sign {
    margin-right: 21px;
    @include tablet {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
  &__cart {
    @include tablet {
      margin-top: 10px;
      margin-bottom: 10px;
    }
    position: relative;
    margin-right: 16px;
    span {
      position: absolute;
      top: -4px;
      right: -4px;
      font-family: 'Quicksand';
      font-weight: 700;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.75px;
      text-transform: uppercase;
      color: var(--light-color);
      background-color: var(--red-color);
      border-radius: 99px;
      width: 20px;
      height: 20px;
      text-align: center;
      padding-left: 1px;
    }
  }
}

.nav {
  margin-top: 20px;
  @include tablet {
    display: none;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    @include tablet {
      flex-direction: column;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    text-align: center;
    letter-spacing: 0.018em;
    color: #787885;
    &--active {
      color: #2979FF;
    }
   }
}

@include tablet {
  .burger { @include burger }
}

.menu--active {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 100vh;
  width: 100%;
  background-color: var(--light-color);

}
