// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 1500px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --blue-color : #2264D1;
  --aqua-color: #76A9FF;
  --grey-color: #787885;
  --red-color: #F44336;
  --green-color: #37833B;
  --lime-color: #ECF7ED;
  --black-color: #19191D;
  --dark-color: #000;
}
