.shop {
  position: relative;
  padding-bottom: 96px;
  &__inner {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  &__sidebar {
    grid-column: 3 span;
    transition: all 0.5s;
    @include tablet {
      background-color: var(--light-color);
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
      transform: translateX(-100%);
      grid-column: 12 span;
      width: 100%;
      height: 100vh;
    }
    &--active {

      transform: translateX(0);
    }
  }
  &__title {
    position: relative;
    display: inline-block;
    padding: 8px 60px 8px 72px;
    border: 1px solid #9DC2FF;
    border-radius: 8px;
    font-family: 'Quicksand';
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: #2264D1;
    @include big-tablet {
      padding: 8px 45px 8px 45px;
    }
    &::before {
      content: '';
      position: absolute;
      left: 12px;
      top: 50%;
      width: 32px;
      height: 32px;
      transform: translateY(-50%);
      @include bg-cover;
      background-image: url('../img/filter.svg');
    }
  }
  &__main {
    grid-column: 9 span;
    @include tablet {
      grid-column: 12 span;
    }
  }
  &__items {
    box-shadow: 0px 1px 2px rgba(58, 58, 68, 0.24), 0px 2px 4px rgba(90, 91, 106, 0.24);
    border-radius: 8px;
    padding: 16px;
  }
  &__start {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__form {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
    }
  }
  &__select {
    margin: 0;
    border: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #19191D;

    &--blocked {
      margin-left: 16px;
      border: 1px solid #B4B4BB;
      border-radius: 4px;
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      letter-spacing: 0.018em;
      color: var(--grey-color);
      margin-top: 6px;
      padding: 7px 12px;
      @include small-tablet {
        margin-left: 0;
      }
    }
  }
  &__option {
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #19191D;
    &--blocked {
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      letter-spacing: 0.018em;
      color: var(--grey-color);
    }
  }
  &__right {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    @include big-desktop {
      margin-top: 10px;
    }
  }
  &__btns {
    background-color: #EBF2FF;
    border-radius: 4px;
    margin-right: 24px;
    @include mobile {
      margin: 0;
      margin-bottom: 20px;
    }
  }
  &__button {
    padding: 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #5A5B6A;
    transition: all 0.3s;
    @include mobile {
      padding: 13px;
    }
    &--active {
      font-weight: 500;
      color: var(--blue-color);
      box-shadow: 0px 1px 2px rgba(27, 78, 163, 0.24), 0px 2px 4px rgba(41, 121, 255, 0.24);
      border-radius: 4px;
      background-color: var(--light-color);
      fill: var(--blue-color);
    }
    &-icon {
      opacity: 0.5;
      padding: 9px 15px;
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      letter-spacing: 0.018em;
      color: #5A5B6A;
      transition: all 0.3s;
      &--icons {
      opacity: 1;
      font-weight: 500;
      color: var(--blue-color);
      box-shadow: 0px 1px 2px rgba(27, 78, 163, 0.24), 0px 2px 4px rgba(41, 121, 255, 0.24);
      border-radius: 4px;
      background-color: var(--light-color);
      fill: var(--blue-color);
    }
  }
    &--grid {
      padding: 4px 20px;

    }
    &--line {
      padding: 8px 20px;

    }
  }
  &__pos {
    background-color: #EBF2FF;
    border-radius: 4px;
    display: flex;
  }
  &__btn-icon  {
    width: 28px;
    height: 28px;
    &--active {
      opacity: 1;
    }
  }
  &__tags {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 40px;
    &-title {
      display: inline-block;
      font-weight: 500;
      font-size: 14px;
      line-height: 143%;
      letter-spacing: 0.018em;
      color: #19191D;
    }
  }
  &__tag {
    background-color: #EDEDF0;
    border-radius: 99px;
    margin-left: 8px;
    padding: 6px 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    text-align: center;
    letter-spacing: 0.018em;
    color: #4A4B57;
    @include tablet {
      margin-top: 10px;
    }
    }
    &__middle {
      margin-top: 16px;
      &--line {
        display: flex;
        flex-direction: column;
        align-items: center;
        animation-name: fadeIns;
        animation-duration: 1s;
      }
      &--grid {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 16px;
        animation-name: fadeIn;
        animation-duration: 1s;
      }
      @include big-desktop {
        gap: 16px  ;
      }
      @include tablet {
        gap: 16px;
      }
    }

    @keyframes fadeIn {
      0% { opacity: 0; }
      20% { opacity: 0; }
      40% { opacity: 0.3; }
      60% { opacity: 0.5; }
      80% { opacity: 0.9; }
      100% { opacity: 1; }
      }

      @keyframes fadeIns {
        0% { opacity: 0; }
        20% { opacity: 0; }
        40% { opacity: 0.3; }
        60% { opacity: 0.5; }
        80% { opacity: 0.9; }
        100% { opacity: 1; }
        }

    &__item {
      grid-column: 3 span;
      transition: flex 0.3s;
      &--line {
        display: flex;
        align-items: center;
        width: 100%;
        margin-top: 20px;
        @include small-tablet {
          flex-direction: column;
        }
      }
      @include big-tablet {
        grid-column: 4 span;
      }
      @include small-tablet {
        grid-column: 6 span;
      }
      @include mobile {
        grid-column:  12 span;
      }
      &:hover {
        box-shadow: 0px 2px 4px rgba(59, 69, 123, 0.2), 0px 4px 8px rgba(92, 107, 192, 0.2);
        border-radius: 8px;
      }

    }
    &__content {
      display: flex;
      flex-direction: column;
      height: 50%;
      padding: 12px 16px 12px 16px;
      &-title {
        flex: 1 1 auto;
        margin: 0;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 0.0275em;
        color: var(--black-color);
        @include big-desktop {
          font-size: 12px;
        }
      }
      &-descr {
        display: flex;
        align-items: center;
        height: 40px;
        margin: 0;
        font-weight: 400;
        font-size: 14px;
        line-height: 143%;
        letter-spacing: 0.018em;
        color: var(--grey-color);
        @include big-desktop {
          font-size: 12px;
        }
      }
      &-button {
        padding: 4px 12px;
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 14px;
        line-height: 143%;
        letter-spacing: 0.018em;
      }
      &-bottom {
        margin-top: 9px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        @include big-tablet {
          margin-top: 0;
          margin-bottom: 10px;
        }
      }
    }
    &__rating {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    &__info {
      margin-top: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__image {
      max-height: 224px;
    }
    &__star {
      margin-bottom: 4px;
    }
    &__price {
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      color: rgba(0, 0, 0, 0.87);
      @include big-desktop {
        font-size: 20px;
      }
    }
    &__about {
      font-family: 'Roboto';
      padding: 4px 6px;
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.4px;
      border-radius: 4px;
      &--pink {
        color: #C23564;
        background-color: #FDEDF2;
      }
      &--green {
        color: var(--green-color);
        background-color:var(--lime-color);
      }
    }
    &__total {
      margin-left: 8px;
      font-family: 'Roboto';
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0.1px;
      color: #5E6366;
    }
    &__bottom {
      margin-top: 40px;
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 16px;
      &-item {
        padding: 8px 16px 24px 16px;
        grid-column: 4 span;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #EDEDF0;
        border-radius: 8px;
        @include tablet {
          grid-column: 6 span;
        }
        @include mobile {
          grid-column: 12 span;
        }
      }
      &-title {
        margin: 0;
        font-family: 'Quicksand';
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        text-align: center;
        letter-spacing: 0.0075em;
        color: rgba(0, 0, 0, 0.87);
      }
      &-descr {
        font-weight: 400;
        font-size: 14px;
        line-height: 143%;
        text-align: center;
        letter-spacing: 0.018em;
        color: #5A5B6A;
      }
    }
    &__menu {
      position: relative;
      margin-left: 32px;
      transition: all 0.3s;

      cursor: pointer;
      &::before {
        content: '';
        position: absolute;
        left: -32px;
        top: 8px;
        width: 24px;
        height: 24px;
        @include bg-cover;
        background-image: url('../img/arrow-close.svg');
        transition: all 0.3s;
      }
      &--active::before {
        content: '';
        transform: rotate(90deg);
      }
    }
    &__submenu {
      position: relative;
      margin-left: 15px;
      overflow: hidden;
      cursor: pointer;
      max-height: 0;
      opacity: 0;
      transition: all 0.5s ease-out;
      &--active {
        opacity: 1;
        max-height: 500px;
        overflow: visible;
        transition: all 0.5s ease-in;
      }
      &--opened::before {
        transform: rotate(90deg);
      }
      &::before {
        content: '';
        position: absolute;
        left: -32px;
        top: 0;
        width: 24px;
        height: 24px;
        @include bg-cover;
        background-image: url('../img/arrow-close.svg');
        transition: all 0.3s;
      }



      &-level2 {
        overflow: hidden;
        position: relative;
        margin-left: 15px;
        cursor: pointer;
        max-height: 0;
        opacity: 0;
        transition: all 0.5s ease-out;
        &--active {
          opacity: 1;
          max-height: 500px;
          overflow: visible;
          transition: all 0.5s ease-in;
        }
        &--opened::before {
          transform: rotate(90deg);
        }
          &::before {
          content: '';
          position: absolute;
          left: -32px;
          top: 0;
          width: 24px;
          height: 24px;
          @include bg-cover;
          background-image: url('../img/arrow-close.svg');
          transition: all 0.3s;
        }

        &:nth-child(3) {
          &::before {
            content: '';
            position: absolute;
            left: -32px;
            top: 8px;
            width: 24px;
            height: 24px;
            @include bg-cover;
            background-image: url('../img/arrow-close.svg');
          }
        }
        &:nth-child(5) {
          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 0;
            height: 0;
          }
        }
      }
      &-level3 {
        overflow: hidden;
        margin-left: 15px;
        max-height: 0;
        opacity: 0;
        transition: all 0.5s ease-out;
        &--active {
          opacity: 1;
          max-height: 500px;
          overflow: visible;
          transition: all 0.5s ease-in;
        }
      }
    }
    &__sidebar {
      &-list {
        margin-top: 32px;
      }
      &-content {
        margin-left: 8px;
        display: flex;
        align-items: center;
      }
      &-item {
        margin-bottom: 10px;
        padding: 8px;
        &--active {
          background-color: var(--blue-color);
          border-radius: 4px;
          box-shadow: none;
          max-width: 304px;
        }
      }
      &-sublist {
        height: 0;
        overflow: hidden;
        transition: all 0.5s;
      }
    &-btn {
      font-family: 'Roboto';
      font-weight: 400;
      position: relative;
      text-align: left;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: 0.022em;
      color: #19191D;
      width: calc(100% - 75px);
      padding: 10px 12px;
      transition: all 0.3s;
      border-radius: 4px;
      box-shadow: 0px 1px 2px rgba(58, 58, 68, 0.24), 0px 2px 4px rgba(90, 91, 106, 0.24);
      @include big-tablet {
        font-size: 14px;
        width: 100%;
      }
      &--active {
        font-family: 'Inter';
        font-weight: 500;
        box-shadow: none;

      }
      &::after {
        content: '';
        position: absolute;
        right: 8px;
        top: 50%;
        transform: translateY(-50%);
        height: 24px;
        width: 24px;
        @include bg-cover;
        background-image: url('../img/sidebar-btn-open.svg');
        transition: all 0.3s;
      }
      &--active::after {
          content: '';
          position: absolute;
          right: 8px;
          top: 50%;
          transform: translateY(-50%);
          height: 24px;
          width: 24px;
          @include bg-cover;
          background-image: url('../img/sidebar-opened.svg');
      }
    }
    &-text {
      margin: 0;
      margin-left: 8px;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: 0.0275em;
      color: #19191D;

    }
    }
    &__list {
      &-info {
        position: relative;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        align-items: center;
        letter-spacing: 0.0275em;
        color: #19191D;
      }
      &-descr {
        margin: 0;
        font-weight: 400;
        font-size: 14px;
        line-height: 143%;
        letter-spacing: 0.018em;
        color: var(--grey-color);
      }
      &-btn {
        position: relative;
        font-family: 'Roboto';
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.75px;
        text-transform: capitalize;
        color: var(--blue-color);
        background: #EBF2FF;
        border-radius: 4px;
        padding: 6px 24px 6px 12px;
        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 28px;
          height: 28px;
          @include bg-cover;
          background-image: url('../img/btn-open.svg');
        }
      }
    }
    &__menu {
      &-btn {
        position: relative;
        font-family: 'Roboto';
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.75px;
        text-transform: capitalize;
        color: var(--blue-color);
        background: #EBF2FF;
        border-radius: 4px;
        padding: 8px 12px 8px 12px;
      }
    }


}

fieldset {
  padding: 0 12px 6px 12px;
  border: 1px solid #5A5B6A;
  border-radius: 4px;
}

legend {
  padding: 0 2px;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 10px;
  line-height: 16px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: var(--grey-color);
}


.sidebar__open-btn {
  display: none;
  @include tablet {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 1001;
    transform: rotate(90deg);
    transition: all 0.5s;
    &--active {
      transform: rotate(270deg);
    }
  }
}

.checkbox-round {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: transparent;
  border-radius: 4px;
  vertical-align: middle;
  border: 2px solid #4A4B57;
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
  transition: all 0.3s;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 50%;
    transition: background 0.3s ease-in-out;
  }
}
.checkbox-round:checked {
  position: relative;
  &::after {
    background-color: var(--blue-color);
  }
}

